body {
  color: #060b35;
  background-color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.main {
  box-sizing: border-box;
  grid-template-columns: repeat(4, 200px);
  gap: 48px;
  width: 1024px;
  max-width: 1200px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 480px) {
  .main {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (width >= 481px) and (width <= 767px) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .main {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 1fr);
    margin: 0 auto;
  }
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  object-fit: contain;
  width: 40px;
  height: 40px;
}

.logo__image {
  width: 100%;
  height: 100%;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin: 0 0 0 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  white-space: nowrap;
  color: #060b35;
  align-items: center;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s ease-in-out;
  display: flex;
  position: relative;
}

.nav__link:link {
  color: #060b35;
}

.nav__link:hover, .is-active, .nav__link:hover, .nav__link.is-active, .is-active:visited {
  color: #00acdc;
}

.nav__link.is-active {
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: color .3s ease-in-out;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 406px;
  font-style: normal;
  transition: transform .3s ease-in-out;
}

.product-card__content {
  padding: 32px 16px 0;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__description {
  line-height: 18px;
}

.product-card__title {
  margin: 0 0 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__rate {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__text {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product-card__label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card:hover {
  z-index: 1;
  transform: scale(1.2);
}

.button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-card:hover .product-card__title {
  cursor: pointer;
  color: #34568b;
}

.product-card-stars {
  align-items: center;
  display: flex;
}

.product-card-stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.product-card-stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

